.header_banner {
    color: #ffffff;
    object-fit: contain;
    height: 448px;
}

.banner_content {
    margin-left: 50px;
    padding-top: 140px;
    height: 190px;
}

.banner_description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;

}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_buttons button:hover {
    background-color:#ffffff ;
    color: rgba(51,51,51,0.5);;
}

.banner_buttons button{
    cursor: pointer;
    color: #ffffff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-bottom: 0.5rem;
    padding-top: 0.5rem;
    margin-left: 3.1rem;
    background-color: rgba(51,51,51,0.5);
    transition: 1s;
}

.banner_fadebottom {
    height: 7.5rem;
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37,37,37,0.65),
        #111
    );
}

@media only screen and (max-width: 460px){
    .banner_description {
        max-width: 200px;
    }
}